.faqContainer {
  display: flex;
  width: 85%;
  margin: 0 auto;
}

.faqSidebar {
  padding: 40px;
  border-right: 1px solid #ccc;
}

.faqSidebar ul {
  list-style-type: none;
  padding: 0;
}

.faqSidebar ul li {
  padding: 10px 0;
  cursor: pointer;
}

.faqSidebar ul li.active {
  color: red;
  font-weight: bold;
}

.faqContent {
  flex: 1;
  padding: 20px;
}

.shippingHeader {
  text-align: center;
  padding-bottom: 75px;
}

.shippingContent {
  padding-left: 150px;
  width: 100%;
  text-align: left;
}
.shippingContent h2{
  font-size: 1.2em;
}
.shippingContent p{
  padding-bottom: 30px;
}
.explainList{
  padding: 10px 10px;
  margin: 16px 10px;
}
.customerServiceContent {
  text-align: center;
  font-size: 0.8em;
}
.customerServiceContent h1{
  margin-bottom: 50px;
}
.faqList {
  text-align: left;
  padding-left: 150px;
}

.faqList ul {
  list-style-type: none;
  padding: 0;
}

.faqList ul li {
  margin-bottom: 20px;
}

.faqList ul li strong {
  display: block;
  margin-bottom: 5px;
}

.faqList h3 {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.faqList span {
  display: block;
  margin-left: 20px;
}

.contactSection {
  text-align: center;
}

.contactHeader {
  text-align: center;
}

.contactWrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 20px;
}

.contactLeftColumn,
.contactRightColumn {
  flex: 1;
  padding: 0 20px;
  text-align: left;
}

.contactWrapper + .contactWrapper {
  margin-top: 20px;
}

.contactLeftColumn {
  padding-left: 250px;
  font-weight: bold;
}

.contactRightColumn {
  border-left: 1px solid #ccc;
  padding-left: 75px;
}